
body{
    font-family: Raleway;
    color: rgb(255, 255, 255);
    background: #ff2424;
    margin: 10px 30px;
}
input[type="text"],input[type="number"] {
    display: block;
    width: 300px;
    margin: 20px 0;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #ddd;
}
label{
    font-weight: bold;
}
form{
    max-width: 350px;
    margin: 30px auto;
}
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333333;
}
li {
    float: left;
}
li a {
    display: block;
    color: white;
    text-align: center;
    padding: 16px;
    text-decoration: none;
}
li a:hover {
    background-color: #111111;
}
textarea {
	width: 300px;
	height: 120px;
  border: 3px solid #cccccc;
  font-size: 20px;
	padding: 5px;
	font-family: Raleway;
	background-position: bottom right;
	background-repeat: no-repeat;
}
button{
    float: right;
    background: rgb(0, 153, 255);
    border: 0;
    padding: 5px 10px;
    color: rgb(3, 0, 44);
    display: inline-block;
    border-radius: 4px;
    cursor: pointer;
}
button:hover{
  background: rgb(2, 59, 97);
}
.pokemon{
  font-family: Raleway;
    max-width: 800px;
    margin: 60px auto;
    padding: 10px 20px;
    color: rgb(0, 0, 0);
    background: #fdfdfd;
    border-radius: 4px;
    -webkit-box-shadow: 1px 2px 3px rgba(0,0,0,0.2);
            box-shadow: 1px 2px 3px rgba(0,0,0,0.2);
}
















































































































































































